import React from 'react';
import { Link } from 'react-router-dom';
import { Alert, Button } from 'reactstrap';
import { useQuery } from '@tanstack/react-query';
import useAxios from '../../hooks/useAxios';
import Tenders from './Tenders';

const Dashboard = () => {
  const api = useAxios();

  const { isLoading, error, data } = useQuery({
    queryKey: ['statusper'],
    queryFn: () =>
      api.get(`vapi/perusahaan/status`).then((res) => {
        return res.data.data;
      }),
  });

  return (
    <>
      {isLoading ? (
        'Loading...'
      ) : error ? (
        'Something went wrong.'
      ) : data?.status_verifikasi === 'terverifikasi' ? (
        <>
          <Tenders />
        </>
      ) : data?.status_verifikasi === 'review_submit' ||
        data?.status_verifikasi === 'review_update' ? (
        <>
          {/* <Alert className="rounded-4" color="danger">
            Akun Anda belum terverifikasi! Cek komentar PVD secara berkala!
          </Alert> */}
          <Alert className="rounded-4 text-info" color="info">
            <div className="d-flex justify-content-between align-items-center">
              Data Perusahaan Anda sedang direview oleh tim Komersial PT PEMA. Silakan cek email
              secara berkala untuk melihat status selanjutnya.
            </div>
          </Alert>
        </>
      ) : data?.status_verifikasi === 'revisi' ? (
        <Alert className="rounded-4 text-danger" color="danger">
          <div className="d-flex justify-content-between align-items-center">
            Admin telah merevisi data perusahaan Anda. Silakan cek email untuk melihat detailnya dan
            lengkapi data selanjutnya di menu Data Perusahaan.
          </div>
        </Alert>
      ) : (
        <Alert className="rounded-4 text-danger" color="danger">
          <div className="d-flex justify-content-between align-items-center">
            Anda Belum Mengisi Data Perusahaan, Silakan lengkapi data perusahaan Anda.
            <Link to="/registration">
              <Button type="button" outline>
                Selengkapnya
              </Button>
            </Link>
          </div>
        </Alert>
      )}
    </>
  );
};

export default Dashboard;
